* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

body {
  background-color: var(--color-bg);
  font-family: "Barlow Semi Condensed", sans-serif;
  line-height: 1.5;
  font-weight: 500;
}

:root {
  --color-bg: #ecf2f8;
  --color-white: #fff;
  --color-violet: #7541c8;
  --color-light-grey: #cfcfcf;
  --color-dark-grey: #19212e;
  --color-grey: #48556a;
}

img {
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid var(--color-white);
}

h1 {
  font-size: 3.9rem;
  margin: 4rem 0;
  font-weight: bold;
}

.testimonial .author {
  display: flex;
  align-items: center;
}
.testimonial .author__image {
  margin-right: 1.6rem;
}
.testimonial .author__name {
  font-size: 1.3rem;
}
.testimonial .author__status {
  font-size: 1rem;
}
.testimonial__item {
  padding: 2rem;
  border-radius: 5px;
}
.testimonial__heading {
  font-size: 2rem;
  margin: 2rem 0;
}
.testimonial__detail {
  font-size: 1.2rem;
}
.testimonial__detail--light-grey {
  color: var(--color-light-grey);
}
.testimonial__detail--dark-grey {
  color: var(--color-dark-grey);
}

.testimonial {
  width: 90%;
  max-width: 160rem;
  margin: 2rem auto;
}
.testimonial__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
}
@media screen and (max-width: 1024px) {
  .testimonial__container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .testimonial__container {
    grid-template-columns: auto;
    grid-template-rows: auto;
  }
}
.testimonial__item--violet {
  grid-row: 1/2;
  grid-column: 1/3;
  background-color: var(--color-violet);
  color: var(--color-white);
}
@media screen and (max-width: 1024px) {
  .testimonial__item--violet {
    grid-column: 1/3;
    grid-row: auto;
  }
}
.testimonial__item--grey {
  grid-row: 1/2;
  grid-column: 3/4;
  background-color: var(--color-grey);
  color: var(--color-white);
}
@media screen and (max-width: 1024px) {
  .testimonial__item--grey {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
.testimonial__item--white-tall {
  grid-row: 1/3;
  grid-column: 4/5;
  background-color: var(--color-white);
  color: var(--color-grey);
}
@media screen and (max-width: 1024px) {
  .testimonial__item--white-tall {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}
.testimonial__item--white {
  grid-row: 2/3;
  grid-column: 1/2;
  background-color: var(--color-white);
  color: var(--color-grey);
}
@media screen and (max-width: 1024px) {
  .testimonial__item--white {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
.testimonial__item--dark-grey {
  grid-row: 2/3;
  grid-column: 2/4;
  background-color: var(--color-dark-grey);
  color: var(--color-white);
}
@media screen and (max-width: 1024px) {
  .testimonial__item--dark-grey {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}
@media screen and (max-width: 768px) {
  .testimonial__item {
    grid-column: auto;
    grid-row: auto;
  }
}

footer {
  margin: 4rem 0;
  font-size: 3rem;
  text-align: center;
}
footer a {
  font-size: inherit;
}